import { BoxShadowProperty } from "csstype"
import SiteLink from "src/components/link"
import VideoContainer from "src/components/VideoContainer"
import { PostFrontmatter } from "src/utils/types"
import { g, React } from "src/utils/view"

type Props = {
  timeToRead: number
  frontmatter: PostFrontmatter
  index?: number
}

export function CompactPost({ index, frontmatter, timeToRead }: Props) {
  return (
    <Container index={(index || 0) % 4}>
      {frontmatter.video && <VideoContainer src={frontmatter.video} />}
      <SiteLink
        to={frontmatter.url}
        newTab={frontmatter.type === "remark-slide"}
      >
        <Header>{frontmatter.title}</Header>
      </SiteLink>
      <Footer>
        ({timeToRead} min read) <br />
        {frontmatter.author} - {frontmatter.date}
        <br />
        {frontmatter.version && `v${frontmatter.version}`}
      </Footer>
    </Container>
  )
}

type ContainerProps = {
  index: number
}

const boxShadows: BoxShadowProperty[] = [
  "inset 10em -3em #EEE",
  "inset -10em -3em #EEE",
  "inset 10em 3em #EEE",
  "inset -10em 3em #EEE",
]

const Container = g.div<ContainerProps>(
  {
    padding: 5,
  },
  ({ index }: ContainerProps) => ({
    display: "flex",
    flexDirection: "column",
    boxShadow: boxShadows[index],
    padding: 5,
  })
)

const Header = g("h1")({})

const Footer = g("span")({
  textAlign: "left",
  color: "gray",
})
